        filters: {
            dateTimeFmt: function (value) {
                if (!value) {
                    return ''
                }
                var date = new Date(value)
                var month = date.getMonth() + 1
                var day = date.getDate()
                if (month < 10) {
                    month = '0' + month
                }
                if (day < 10) {
                    day = '0' + day
                }
                var hh = date.getHours()
                var mm = date.getMinutes()
                var ss = date.getSeconds()
                if (hh < 10) {
                    hh = '0' + hh
                }
                if (mm < 10) {
                    mm = '0' + mm
                }
                if (ss < 10) {
                    ss = '0' + ss
                }
                return date.getFullYear() + '-' + month + '-' + day
            }
