import axios from 'axios';
import qs from 'qs';
// import { Message } from 'element-ui';

const service = axios.create({
    // process.env.NODE_ENV === 'development' 来判断是否开发环境
    // easy-mock服务挂了，暂时不使用了
    // baseURL: process.env.VUE_APP_BASE_URL,
    //baseURL:"http://124.202.145.102:8097/wastesort/",

    timeout: 5000
});

// 添加请求拦截器
service.interceptors.request.use(
    config => {
        // config.headers
        // config.headers="{'Content-Type':'application/x-www-form-urlencoded'}";
        // config.baseURL="http://124.202.145.102:8097/wastesort/";

        //console.log("-----config:",config);
        //console.log("-----config.data:",config.data);
        if (config.method === 'post') {
            //普通post请求参数处理
            config.headers['Content-Type'] = 'application/x-www-form-urlencoded';
            config.data = qs.stringify(config.data,{ indices: false });

            config.paramsSerializer = params => {
                //params = params['_'] = (new Date()).getTime();
                return qs.stringify(params, { arrayFormat: 'repeat' })
            }
            // config.
        } else if (config.method === 'post-json') {
            //post-json请求
            config.method = 'post';
            config.headers['Content-Type'] = 'application/json';
        } else if (config.method === 'post-img') {
            //post-文件上传请求
            config.method = 'post';
            config.headers['Content-Type'] = 'multipart/form-data';
        }
        else if(config.method === 'get'){
            config.method ='get';
            config.baseURL='';
        }

        return config;
    },
    error => {
        console.log(error);
        return Promise.reject();
    }
);

// 添加响应拦截器
service.interceptors.response.use(
    response => {
        //console.log("响应拦截器状态位：",response.status);
        if (response.status === 200) {
            return response.data;
           /* if (response.data.code == 0) {
                return response.data;
            } else {
                // Message.error(response.data.msg);
                return Promise.reject();
            }*/
        } else {
            // Message.error('');
            return Promise.reject();
        }
    },
    error => {
        console.log(error);
        return Promise.reject();
    }
);

export default service;
